



































































































































































































































































































































@import "../../assets/scss/_vars.scss";

.date-wrapper, .sent-wrapper {
    clear: both;

    .date-sent, .sending {
        float: right;
        margin-right: 36px;
    }

    .date-received {
        float: left;
        margin-left: 36px;
    }
}

.message-wrapper {
    user-select: text;
    -moz-user-select: text; 
    -ms-user-select: text;
    clear: both;
    display: block;

    .message {
        position: relative;
        padding: 16px;
        margin: 4px 8px 4px 8px;
        max-width: 310px;
        border-radius: 15px;
        box-shadow: -1px 2px 4px rgba(0, 0, 0, .1);
        overflow-wrap: break-word;
        word-wrap: break-word;
        min-width: 18px;
    }

    .message_options {
        margin-top: 12px;
        color: rgba(0,0,0,.25);
    }

    .sent_options {
        float: right;
    }

    .media-preview {
        text-align: center;
        margin: auto;
        margin-top: 8px;
        margin-bottom: 8px;
        background: white;
        color: black;
        box-shadow: 1px 2px 4px rgba(0, 0, 0, .1);
    }

    .article-title, .article-snippet {
        color: black;
        text-align: left;
    }

    .message:after {
        content: "";
        position: absolute;
        bottom: -20px;
        left: 50%;
        border-style: solid;
        display: block;
        top: 0px;
        bottom: auto;
    }

    .media-preview:after {
        display: none;
    }

    .received {
        float: left;
        color: #fff;
        margin-left: 28px;
        background: #fff;
        border-color: red transparent;
        box-shadow: 1px 2px 4px rgba(0, 0, 0, .2);

        &:after {
            left: -8px;
            border-width: 37px 0 0 22px;
            border-color: inherit;
        }

        a {
            color: white;
        }
    }

    .sent {
        float: right;
        color: black;
        margin-right: 28px;
        background: #fff;

        &:after {
            right: -8px;
            border-width: 15px 10px 0 0;
            border-color: #fff transparent;
        }

        a {
            color: black;
        }
    }

    .error {
        float: right;
        color: black;
        margin-right: 28px;
        background: #F44336;
        color: #fff;

        &:after {
            right: -8px;
            border-width: 15px 10px 0 0;
            border-color: #F44336 transparent;
        }
    }

    .info {
        text-align: center;
        margin: auto;

        &:after {
            border-color: transparent transparent;
        }
    }

    .media {
        width: 250px;
        height: 250px;
        background-repeat: no-repeat;
        object-fit: cover;
    }

    a {
        text-decoration: none;
    }
    .article-title {
        margin-top: 8px;
        font-size: 15px;
        font-weight: normal;
    }

    .article-snippet {
        margin-top: 12px;
        font-size: 14px;
        font-weight: lighter;
    }

    @media screen and (max-width: 350px) {
        .message, .media {
            max-width: 200px;
        }
        .media {
            width: 200px;
        }
    }

    @media screen and (max-width: 450px) {
        .message {
            max-width: 270px;
        }
        .media {
            width: 270px;
        }
    }

    @media screen and (min-width: 600px) {
        .message {
            max-width: 372px;
        }
        .media {
            width: 372px;
        }
    }

    @media screen and (min-width: 720px) {
        .message {
            max-width: 436px;
        }
        .media {
            width: 436px;
        }
    }
}

img {
  padding-bottom: 4px;
}

video {
    object-fit: cover;
    background-repeat: no-repeat;
    height: 400px;
}

.slide-out-enter-active, .slide-out-leave-active {
    transition: transform .3s ease-in, opacity .3s ease-in;
    position: relative;
    z-index: -10;
}

.slide-out-enter, .slide-out-leave-to {
    transform: translateY(-30px);
    opacity: 0;
}

.fade-enter-active, .fade-leave-active {
    transition: opacity .3s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
}

body.dark {
    .sent {
        background: $bg-darker;
        color: #fff;

        &:after {
            border-color: $bg-darker transparent;
        }
    }

    .message_options {
        margin-top: 12px;
        color: rgba(255,255,255,.25);
    }
}

