











































































.lightbox-toolbar {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1001;
}

.lightbox-wrapper {
    height: 100%;
}

.lightbox-overlay {
    text-align: center;
    background-color: rgba(0,0,0,1);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.full-image {
    display: flex;
    height: auto;
    width: auto;
    max-height: 100%;
    max-width: 100%;
}

#download-button {
    float: right;
    margin-right: 10px;
    margin-top: 20px;
    color: white;
    z-index: 1001;
}

#close-button {
    float: right;
    margin-right: 20px;
    margin-top: 20px;
    color: white;
    z-index: 1001;
}

