












































































































@import "../../assets/scss/_vars.scss";

.date-wrapper {
    clear: both;

    .date {
        float: right;
        margin-right: 36px;
    }
}

.message-wrapper {
    user-select: text;
    clear: both;
    display: block;

    .message {
        position: relative;
        padding: 16px;
        margin: 4px 8px 4px 8px;
        max-width: 310px;
        border-radius: 15px;
        overflow-wrap: break-word;
        word-wrap: break-word;
        min-width: 18px;
    }

    .message:hover {
        cursor: pointer;
    }

    .message:after {
        position: absolute;
        bottom: -20px;
        left: 50%;
        border-style: solid;
        display: block;
        top: 0px;
        bottom: auto;
    }

    .scheduled {
        float: right;
        color: black;
        margin-right: 28px;
        background: #fff;

        &:after {
            right: -8px;
            border-width: 15px 10px 0 0;
            border-color: #fff transparent;
        }

        a {
            color: black;
        }
    }

    @media screen and (min-width: 600px) {
        .message {
            max-width: 372px;
        }
    }

    @media screen and (min-width: 720px) {
        .message {
            max-width: 436px;
        }
    }
}

body.dark {
    .scheduled {
        background: $bg-darker;
        color: #fff;

        &:after {
            border-color: $bg-darker transparent;
        }
    }
}

