.label-item {
    padding-left: 15px;
}

.item, .click-item {
    position: relative;
    width: 100%;
    padding: 16px;
    line-height: 18px;
}

.item:hover, .click-item:hover {
	  background: #E0E0E0;
}

.click-item:hover {
    cursor: pointer;
}

body.dark {
    .item:hover, .click-item:hover {
    	  background: #202020;
    }
}

.mdl-dialog {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
}

.mdl-dialog-card {
    width: 300px;
    min-height: 120px;
    margin: auto;
    margin-top: 100px;
    text-align: left;
}

.mdl-dialog-button-bar {
  	margin-left: auto;
  	margin-right: 24px;
}

