























































































































@import "../../assets/scss/_vars.scss";

body.dark .conversation-card {
    background: $bg-dark;

    &.mdl-card {
        background: $bg-darker;
    }


    &.small.mdl-card {
        background: $bg-dark;
    }

    .conversation-text {
        .conversation-title {
            color: white;
        }

        .conversation-snippet {
            color: rgba(255,255,255,.77);
        }
    }
}

body.black .conversation-card {
    background: $bg-black;

    &.mdl-card {
        background: $bg-black;
    }


    &.small.mdl-card {
        background: $bg-black;
    }

    .conversation-text {
        .conversation-title {
            color: white;
        }

        .conversation-snippet {
            color: rgba(255,255,255,.77);
        }
    }
}

.conversation-card {
    &.mdl-card {
        display: block;
        min-height: 80px;
        width: 100%;
        cursor: pointer;
        margin-top: -1px;
        border-radius: 2px;
    }

    .contact-img {
        width: 49px;
        height: 49px;
        float: left;
        margin: 16px;
    }

    .conversation-text {
        margin-top: 18px;
        height: 52px;
        margin-right: 16px;
        overflow: hidden;

        &.unread {
            font-weight: bold;
        }

        .conversation-title {
            color: black;
            font-size: 16px;
            padding: 24px 16px 0px 0px;
            white-space: nowrap;
            overflow: hidden;
        }

        .conversation-title i {
            width: 8px;
            height: 8px;
            display: inline-block;
            position: relative;
            margin-right: 5px;
            border-radius: 50%;
            background-color: #2196f3;
        }

        .conversation-snippet {
            font-size: 14px;
            padding: 0px 16px 24px 0px;
        }
    }

    &.small {
        min-height: 56px;
        height: 56px;
        background: $bg-light;
        box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
        margin-top: 0px;

        .contact-img {
            width: 25px;
            height: 25px;
        }

        .conversation-text {
            margin-top: 9px;
            height: 48px;
            line-height: 15px;
            white-space: nowrap;

            .conversation-title {
                font-size: 14px;
            }

            .conversation-snippet {
                font-size: 13px;
            }
        }
    }
}
