@import "./assets/scss/_vars.scss";

body {
    margin: auto;
    margin-left: 0;
    color: #202020;
    background-color: $bg-light;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 14px;
    padding: 0 !important;
    margin-bottom: 0 !important;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-smooth: always;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.file-drag.dragging {
    border: rgba(0,128,0,0.3) solid 1em;
    position: fixed;
    height: calc(100% - 2em);
    width: calc(100% - 2em);
    top: 0;
    z-index: 10;
    left: 0;
}

#toolbar {
    height: 43px;
    top: 0;
    position: fixed;
    z-index: 4;
    width: 100%;
    border-bottom: solid 1px #ca2100;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
    background-color: $bg-light;
    border-color: #e3e3e3;
}

#toolbar_inner {
    max-width: 950px;
    height: 100%;
    position: relative;
    z-index: 6;
    transition: ease-in-out margin-left $anim-time;

    #logo {
        float: left;
        margin-left: 20px;
        padding-top: 9px;

        #logo-image:hover {
            cursor: pointer;
        }

        .icon {
            margin-top: 2px;
            width: 25px;
            height: 25px;

            &.logo {
                background: url(assets/images/vector/pulse.svg) 0 0 no-repeat;
            }

            &.logo_dark {
                background: url(assets/images/vector/pulse-dark.svg) 0 0 no-repeat;
            }

            &.menu_toggle {
                background: url(assets/images/vector/menu_toggle.svg) 0 0 no-repeat;
            }

            &.menu_toggle_dark {
                background: url(assets/images/vector/menu_toggle-dark.svg) 0 0 no-repeat;
            }
        }

    }

    .mdl-menu__outline {
        border-radius: 10px;
    }

    .mdl-layout-title {
        float: left;
        margin-left: 15px;
        margin-top: 12px;
    }

    #toolbar-title {
        white-space: nowrap;
        overflow: hidden;
        height: 48px;
    }

    @media screen and (min-width: 150px) {
        .mdl-layout-title {
            max-width: 200px;
        }
    }

    @media screen and (min-width: 350px) {
        .mdl-layout-title {
            max-width: 160px;
        }
    }

    @media screen and (min-width: 600px) {
        .mdl-layout-title {
            max-width: 420px;
        }
    }

    @media screen and (min-width: 720px) {
        .mdl-layout-title {
            max-width: 520px;
        }
    }

    @media screen and (min-width: 1025px) {
        .mdl-layout-title {
            max-width: 720px;
        }
    }
}


#toolbar_icons {
    border: 0;
    float: right;
    height: 100%;
    margin-top: 5px;
    white-space: nowrap !important;

    .mdl-menu, .mdl-menu__outline {
        margin-top: -30px;
        margin-left: -170px;
        transform-origin: right top 0;
    }

}

#wrapper {
    transition: ease-in-out margin-left $anim-time;
    padding-top: 15px;
}

#content {
    transition: ease-in-out margin-left $anim-time;
    max-width: 950px;
    min-height: 380px;
    margin-left: $sidebar_margin; /* TODO, should be dynamic */
    vertical-align: top;

    @media (max-width: $mini_width) {
        & {
            margin-left: 0px;
        }
    }
}

.mdl-layout__content {
    width: 100%;
    height: 100%;
    position: relative;

    @media (min-width: $mini_width) {
        & {
            max-width: 650px;
        }
    }

    .page-content {
        bottom: 0;
        margin: auto;
        margin-bottom: 54px;
        margin-top: 54px;
        padding-top: 16px;
        padding-bottom: 16px;
        overflow: hidden;

        @media screen and (min-width: 720px) {
            & {
                max-width: 720px;
                padding: 16px;
            }
        }

        @media screen and (min-width: 600px) {
            & {
                max-width: 600px;
                padding: 16px;
            }
        }
    }
}

.shadow {
    box-shadow: 0px 2px 2px 0 rgba(0, 0, 0, 0.25);
}


#refresh-button.rotate {
    transition: transform .3s ease-in;
    transform: rotate(360deg);
}

/* splash-fade transition */
.splash-fade-enter-active {
    transition-delay: 1s;
    transition: all $anim-time ease;
}
.splash-fade-leave-active {
    transition-delay: 1s;
    transition: all $anim-time ease;
}
.splash-fade-enter, .splash-fade-leave-to {
    transform: translateY(70%);
    opacity: 0;
}

.list-enter-active, .list-leave-active {
    transition: all .3s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateX(30px);
}

.link-sent {
    color: black;
}

.link-received {
    color: white;
}

.emojione {
    height: 16px;
    width: 16px;
}

.message {
    .emojione {
        height: 24px;
        width: 24px;
    }
}

body.dark {
    background-color: $bg-dark;
    color: #fff;

    .mdl-progress > .bufferbar {
        background-image: linear-gradient(to right,rgba(55, 66, 72,.7),rgba(55, 66, 72,.7)),
            linear-gradient(to right,rgb(33,150,243),rgb(33,150,243));
    }

    #toolbar {
        border-bottom: solid 1px #ca2100;
        background-color: $bg-darker;
        border-color: #202b30;
    }

    #logo .icon {
        &.logo {
            background: url(assets/images/vector/pulse-dark.svg) 0 0 no-repeat !important;
        }

        &.menu_toggle {
            background: url(assets/images/vector/menu_toggle-dark.svg) 0 0 no-repeat !important;
        }

    }

    .mdl-color-text--grey-900 {
        color: #fff !important;
    }

    .mdl-color-text--grey-600 {
        color: rgba(255,255,255,.77) !important;
    }

    .link-sent {
        color: white;
    }
}

body.larger_app_bar {
    #toolbar {
        height: 55px;
    }

    #toolbar_inner {
        margin-top: 7px;
    }

    .menu_icon {
        margin: auto 5px;
    }

    @media screen and (min-width: 350px) {
        .mdl-layout-title {
            max-width: 161px;
        }
    }
}

body.black {
    background-color: $bg-black;

    #toolbar {
        border-bottom: solid 1px #000000;
        background-color: $bg-black;
        border-color: #000000;
    }
}

.transition span.animator {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    overflow: hidden;

    span:first-of-type {
        display: block;
        position: absolute;
        top: -50%;
        width: 200%;
        height: 200%;
        margin-left: -50%;
        border-radius: 50px;
        opacity: 0;
        z-index: 3;
        animation: ripple .5s ease-out forwards;
    }
}

@keyframes ripple {
    0% {
        transform: scaleX(0);
    }
    70% {
        transform: scaleX(0.5);
    }
    100% {
        opacity: 1;
        transform: scaleX(1);
    }
}

