




























































































@import "../../assets/scss/_vars.scss";

.empty-message {
    color: rgba(0, 0, 0, 0.54);
    margin: 6em auto;
    width: 5.5em;
}

#folders-list {
    width: 100%;
    margin-top: 36px !important;

    .spinner {
        margin-top: 100px;
    }
}

.flip-list-enter, .flip-list-leave-to	{
    opacity: 0;
}

.flip-list-leave-active {
    position: absolute;
}

.flip-list-move {
    transition: transform $anim-time;
}
