

























































































































@import "../../assets/scss/_vars.scss";

#create-scheduled-pane {
    position: relative;
    width: 330px;
    height: 100%;
    margin: 5em auto;
}

.time-picker {
    border: transparent;
    font-size: 15px;
    margin-left: 12px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.repeat {
    margin-top: 10px;
    margin-bottom: 10px;
}

.loading-center {
    position: absolute;
    margin: 57px auto;
    background: #fff;

    height: 100%;
    width: 100%;

    text-align: center;
    vertical-align: middle;

    .spinner {
        margin: 35% auto;
        translate: scale(2);
    }
}

/* loading-fade transition */
.loading-fade-enter-active {
    transition-delay: 1s;
    transition: all $anim-time ease;
}
.loading-fade-leave-active {
    transition-delay: 1s;
    transition: all $anim-time ease;
}
.loading-fade-enter, .loading-fade-leave-to {
    transform: translateY(70%);
    opacity: 0;
}

body.dark {
    #create-scheduled-pane {
        background: $bg-darker;

        .mdl-card__title {
            color: #fff;
        }

        .mdl-textfield__input, .mdl-textfield__label, .mdl-card__supporting-text {
            color: #fff;
        }

        .time-picker {
            background: $bg-darker;
            color: #fff;
        }

        .mdl-button {
            color: #fff;
        }
    }

    .loading-center {
        background: $bg-darker;
    }
}

