















@import "../assets/scss/_vars.scss";


.mdl-snackbar { /* Inbox style snackbar */
    display: block !important;
    transform: translate(0%,80px);
    bottom: 24px;
    left: 24px;
    position: fixed;
    z-index: 4500;

    @media (max-width: $mini_width) {
        & {
            bottom: 0;
            left: 0;
            width: 100%;
            max-width: 100%
        }
    }

    &--active {
        transform: translate(0%, 0);
    }

    &__action {
        opacity: 1;
        height: 48px;
        pointer-events: all;
    }
}

