.fade-enter-active, .fade-leave-active {
  transition: opacity .2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

button {
    background: #f3f3f3;
    margin-left: calc(50% - (136.5px)/2);
    margin-bottom: 30px;
}

#message-list {
    transition: margin-bottom 0.3s ease-in-out;
}
