



























































































































































































































































































@import "../../assets/scss/_vars.scss";

    #emoji {
        background: url("../../assets/images/ic_mood.png") no-repeat;
        background-size: cover;
    }

    #attach {
        background: url("../../assets/images/ic_attach.png") no-repeat;
        background-size: cover;
        margin-top: 18px;
        width: 24px;
        height: 24px;
    }

    body.dark {
      #emoji {
          background: url("../../assets/images/ic_mood_white.png") no-repeat;
          background-size: cover;
      }

      #attach {
          background: url("../../assets/images/ic_attach_white.png") no-repeat;
          background-size: cover;
      }
    }

    .send-bar {
        height: auto;
        width: 100%;
        margin: auto;
        position: fixed;
        bottom: 0%;
        clear: both;
        transition: ease-in-out width $anim-time;

        .mdl-progress {
            width: 100%;
        }

        .preview {
            position: relative;
            background: #fafafa;
            max-height: 300px;
            overflow: hidden;
            border-radius: 15px;
            margin-bottom: 10px;
            box-shadow: -0px -0px 3px rgba(0, 0, 0, .15);

            .overlay {
                background: linear-gradient(to bottom, rgba(250,250,250,0) 95%,rgba(250,250,250,1) 100%);
                position: absolute;
                top: 0;
                bottom: 0;
                height: 100%;
                width: 100%;
                z-index: 10;

                .media-clear {
                    position: absolute;
                    margin: 0.3em 1em;
                    background: rgb(33, 150, 243) none repeat scroll 0% 0%;
                    padding: 0.3em;
                    right: 1em;
                    width: 24px;
                    min-width: 24px;
                    min-height: 24px;
                    height: 24px;
                }

                .media-clear i {
                    width: 24px;
                    font-size: 18px;
                    line-height: 24px;
                    height: 24px;
                }
            }

            img {
                margin: 1em calc(24px + 16px + 8px) 1em;
                width: calc(100% - 108px);
            }
        }

        @media (min-width: 750px) {
            & {
                width: 450px;
            }
        }
        @media (min-width: 800px) {
            & {
                width: 490px;
            }
        }
        @media (min-width: 820px) {
            & {
                width: 505px;
            }
        }
        @media (min-width: 850px) {
            & {
                width: 530px;
            }
        }
        @media (min-width: 870px) {
            & {
                width: 550px;
            }
        }
        @media (min-width: 890px) {
            & {
                width: 570px;
            }
        }
        @media (min-width: 920px) {
            & {
                width: 600px;
            }
        }
        @media (min-width:950px) {
            & {
                width: 630px;
            }
        }
    }

    .mdl-textfield {
        padding: 0px 0;
        padding-top: 20px;
    }

    .mdl-textfield__input {
        min-height: 2em;
        transition: height ease-in-out $anim-time;
        outline: none;
    }

    .mdl-textfield__label:after {
        background-color: rgba(0,0,0,0);
        bottom: 1px;
        height: 1px;
    }

    .send-bar-inner {
        background: #fafafa;
        height: 100%;
        margin: auto;
        padding-left: 16px;
        padding-right: 16px;
        padding-top: 4px;
        padding-bottom: 4px;
        border-radius: 16px 16px 0px 0px;
        box-shadow: -0px -0px 3px rgba(0, 0, 0, .15);

        .entry {
            width: calc(100% - 124px);
            margin-top: -4px;
        }

        .send {
            float: right;
            margin-top: 8px;
        }

        .attach-button {
            float: left;
            text-align: center;
            margin-top: 15px;
            min-width: 0px;
            width: 28px;
            height: 28px;
            opacity: .56;
            margin-right: 8px;
        }

        .emoji-button {
            float: left;
            text-align: center;
            margin-top: 18px;
            min-width: 0px;
            width: 24px;
            height: 24px;
            opacity: .56;
            margin-right: 16px;
        }
    }

    #message-entry {
        height: 24px;
        resize: none;
    }

    #emoji-wrapper {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;

        .emoji-mart {
            z-index: 15;
            transition: ease-in-out left $anim-time;
        }
    }

    body.dark {
        .preview {
            background: rgb(55,66,72);

            .overlay {
                background: linear-gradient(to bottom, rgba(55,66,72,0) 95%,rgba(55,66,72,1) 100%);
            }
        }

        .send-bar-inner {
            background: #374248;
            color: #fff;
        }

        .mdl-textfield__label {
            color: #fff !important;
        }
    }

    body.black {
        .preview {
            background: rgb(0,0,0);

            .overlay {
                background: linear-gradient(to bottom, rgba(0,0,0,0) 95%,rgba(0,0,0,1) 100%);
            }
        }

        .send-bar-inner {
            background: #000000;
        }
    }

