


















































@import "../../../assets/scss/_vars.scss";

.item, .click-item {
    position: relative;
    width: 100%;
    padding: 12px;
    line-height: 18px;

    -webkit-user-select: text;
    -moz-user-select: text;
    -ms-user-select: text;
    user-select: text;
}

.item:hover, .click-item:hover {
	  background: #E0E0E0;
}

.click-item:hover {
    cursor: pointer;
}

body.dark {
    .item:hover, .click-item:hover {
        background: #202020;
    }
}

