






















































































@import "../../../assets/scss/_vars.scss";

#auto-reply-list {
    width: 100%;

    .spinner {
        margin-top: 100px;
    }
}

.flip-list-enter, .flip-list-leave-to	{
    opacity: 0;
}

.flip-list-leave-active {
    position: absolute;
}

.flip-list-move {
    transition: transform $anim-time;
}
