

































































































































































































































































































@import "../../assets/scss/auto-complete.css";
@import "../../assets/scss/_vars.scss";

#recipient {
    border-bottom: 1px solid rgba(0,0,0,0);
}

#compose-head {
    width: 100%;
    overflow: visible;
    transition: ease-in-out width 0.5s;
    background: transparent;

    .mdl-card__title {
        padding: 0;
        overflow-x: scroll;
        overflow-y: hidden;
        white-space: nowrap;
    }

    #recipient-wrap {
        display: inline-block;
    }

    .mdl-textfield {
        max-width: 600px;
        min-width: 600px;
        width: 600px;
    }

    .mdl-textfield__label::after {
        visibility: hidden;
    }

    #chip-insert {
        display: inline-flex;
        margin-left: 0px;
    }

    .mdl-chip {
        margin-right: 8px;

        &.selected {
            background-color: #b3d4fc;
        }

        &.error {
            background-color: #d23f31 !important;
        }
    }

    #border {
        position: absolute;
        transition-duration: .2s;
        transition-timing-function: cubic-bezier(.4,0,.2,1);
        background-color: rgba(0, 0, 0, 0.12);
        top: 136px;
        height: 1px;
        left: 0px;
        visibility: visible;
        width: calc(100% - 2em);
        margin: 0 1em 0 1em;
    }
}

#compose-head {
    @media (min-width: 750px) {
        width: 500px;
    }

    @media (min-width: 800px) {
        width: 550px;
    }

    @media (min-width: 850px) {
        width: 600px;
    }

    @media (min-width: 900px) {
        width: 650px;
    }
}

.mdl-textfield {
    @media (min-width: 750px) {
        max-width: 450px;
        min-width: 450px;
        width: 450px;
    }

    @media (min-width: 800px) {
        max-width: 500px;
        min-width: 500px;
        width: 500px;
    }

    @media (min-width: 850px) {
        max-width: 550px;
        min-width: 550px;
        width: 550px;
    }

    @media (min-width: 900px) {
        max-width: 600px;
        min-width: 600px;
        width: 600px;
    }
}

body.dark {
    #compose-head {
        .mdl-card__title {
            color: #fff;
        }

        .mdl-textfield__input, .mdl-textfield__label {
            color: #fff;
        }
    }
}

input:focus {
    outline: none;
}

