




















































































































@import "../assets/scss/_vars.scss";

#login-pane {
    position: relative;
    width: 330px;
    height: 100%;
    margin: 5em auto;
}

.loading-center {
    position: absolute;
    margin: 57px auto;
    background: #fff;

    height: 100%;
    width: 100%;

    text-align: center;
    vertical-align: middle;

    .spinner {
        margin: 35% auto;
        translate: scale(2);
    }
}

.error {
    color: rgb(255,64,129);
}

/* loading-fade transition */
.loading-fade-enter-active {
    transition-delay: 1s;
    transition: all $anim-time ease;
}
.loading-fade-leave-active {
    transition-delay: 1s;
    transition: all $anim-time ease;
}
.loading-fade-enter, .loading-fade-leave-to {
    transform: translateY(70%);
    opacity: 0;
}

